    cancel() {
        if (this.state.MessageCount % 2 === 0) {
            this.setState({ ifMessageShow: true })
        } else {
            this.setState({ ifMessageShow: false })
        }
        let count = this.state.MessageCount;
        count += 1;
        this.setState({ MessageCount: count })
    }
    Message(status) {
        console.log(this.state.MessageCount)
        if (this.state.MessageCount % 2 === 0) {
            this.setState({ ifMessageShow: true })
        } else {
            this.setState({ ifMessageShow: false })
        }
        let count = this.state.MessageCount;
        count += 1;
        this.setState({ MessageCount: count })
        if (status === "success") {
            console.log("success")
            // this.setState({ ifMessageStatus: true })
        } else if (status === "failure") {
            console.log("failure")
            // this.setState({ ifMessageStatus: false })
        }
    }
