    provinceChange(e) {
        let value = e.target.value;
        console.log(value)
        this.setState({ province: value })
        console.log(city)
        console.log(typeof city)

        //////////////////处理 city
        let tempData = city;
        // 将Object的属性输出成Array
        function objOfPropertyToArr(object) {
            var arr = [];
            var i = 0;
            for (var item in object) {
                arr[i] = item;
                i++;
            }
            return arr;
        }
        // 将Object的属性值输出成Array
        function objOfValueToArr(object) {
            var arr = [];
            var i = 0;
            for (var item in object) {
                arr[i] = object[item];
                i++;
            }
            return arr;
        }
        let temp = objOfPropertyToArr(tempData);
        let temp2 = objOfValueToArr(tempData);
        console.log(temp)
        console.log(temp2)
        let i;
        let length = temp.length;
        let tem;
        let temp_data_cityS = [];
        // let myReadStatus;
        // let k = 0; //作用：每当有一个 "[]"，则自增1
        for (i = 0; i < length; i++) {
            let jsonTest = JSON.stringify(temp2[i]);
            if (jsonTest === "[]") {
                continue;
            } else {
                try {
                    tem = {
                        provinceName: temp[i],
                        CityNameArr: temp2[i]
                    };
                    // Vue.set(vm.lists[0].farmers, i, tem); // "[]"情况 - 显示匿名人
                    temp_data_cityS.push(tem)
                } catch (error) {
                    console.log(error);
                }
            }
        } //for
        console.log(tem)
        console.log(temp_data_cityS)
        //////////////////处理 city

        //////////////////处理 temp_data_cityS
        let k;
        for (k = 0; k < length; k++) {
            if (temp_data_cityS[k].provinceName === value) {
                // this.state.data_cityS = temp_data_cityS[k].CityNameArr
                // this.setState({ data_cityS: temp_data_cityS[k].CityNameArr })
                // this.state.cityS = temp_data_cityS[k].CityNameArr
                this.setState({ cityS: temp_data_cityS[k].CityNameArr })
            }
        }
        console.log(this.state.cityS)
    }
