    reg() {
        console.log("立即注册")
        // 所有提示初始化不显示
        this.resErrorInfo()
        console.log(this.state.cooperation)

        // 显示单独提示
        if (this.state.name === "") {
            this.setState({
                error_name: true
            });
        }
        else if (this.state.phone === "") {
            this.setState({
                error_phone: true
            });
        }
        else if (this.state.email === "") {
            this.setState({
                error_email: true
            });
        }
        else if (this.state.position === "") {
            this.setState({
                error_position: true
            });
        }
        else if (this.state.cooperation === "") {
            this.setState({
                error_cooperation: true
            });
        }
        else if (this.state.company === "") {
            this.setState({
                error_company: true
            });
        }
        else if (this.state.industry === "") {
            this.setState({
                error_industry: true
            });
        }
        else if (this.state.employees_num === "") {
            this.setState({
                error_employees_num: true
            });
        }
        else if (this.state.province === "") {
            this.setState({
                error_province: true
            });
        }
        else if (this.state.city === "") {
            this.setState({
                error_city: true
            });
        } else {

            // }
            // 跳转成功
            // let mydata = {
            //     name: "王",
            //     phone: "1872212211",
            //     email: "1234567@qq.com",
            //     position: "前端",
            //     cooperation: "测试类型",
            //     company: "数海知库",
            //     industry: "IT互联网",
            //     employees_num: "15-50人",
            //     province: "贵州",
            //     city: "贵阳",
            //     adress: "高新区",
            //     tracking: "teaser1_cto_banner",
            //     file: "001.pdf"
            // }
            let mydata = {
                name: this.state.name,
                phone: this.state.phone,
                email: this.state.email,
                position: this.state.position,
                cooperation: this.state.cooperation,
                company: this.state.company,
                industry: this.state.industry,
                employees_num: this.state.employees_num,
                province: this.state.province,
                city: this.state.city,
                address: this.state.address,
                // tracking: "teaser1_cto_banner",
                // file: "001.pdf"
                tracking: this.state.tracking,
                file: this.state.file
            }
            console.log(mydata)
            axios({
                url: "https://icampaign.com.cn/customers/veritas/2019megalaunch/reg/submit.php",
                method: "post",
                data: mydata,
                headers: {
                    "Content-type": "multipart/form-data"
                },
            }).then(res => {
                // res = JSON.parse(res);
                console.log(res.status);
                if (res.status === 200 || res.status === "200") {
                    // MessageBox.alert('注册成功', '提示信息');
                    this.Message("success")
                } else {
                    // MessageBox.alert('注册失败，请重试', '提示信息');
                    this.Message("failure")
                }
            }).catch(error => {
                // console.log(error);
            });
        }
    }
